.searchshowcase {
  background: rgba(0, 0, 0, 0.2) url("/images/bukid.jpg") no-repeat fixed;
  background-size: cover;
  background-position: bottom;
  background-blend-mode: multiply;
  height: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.searchshowcase h1 {
  margin: 0 1rem;
  font-size: clamp(2rem, 5.5vmax, 4rem);
  font-weight: 700;
  color: #f5f5f5;
}

.spikes {
  position: relative;
  background: #f5f5f5;
  height: 10vh;
  transform: rotateZ(180deg);
}

.spikes::after {
  content: "";
  position: absolute;
  right: 0;
  left: -0%;
  top: 98%;
  z-index: 10;
  display: block;
  height: 90px;
  background-size: 90px 100%;
  background-image: linear-gradient(135deg, #f5f5f5 25%, transparent 25%),
    linear-gradient(225deg, #f5f5f5 25%, transparent 25%);
  background-position: 0 1px;
}

.ant-carousel img {
  max-height: 250px !important;
  object-fit: cover;
}

.filter {
  white-space: nowrap;
  text-overflow: ellipsis;
  width: 100%;
  overflow: hidden;
}

.resultsDetails__right {
  display: flex !important;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-between;
}

.xs_title {
  margin: 0;
}

.xs_avail {
  padding: 0.5rem;
  display: flex;
  justify-content: space-between;
}
